import { Component, OnInit } from '@angular/core';
import { PostProvider } from '../../providers/post-provider';
import { ActivatedRoute, Router } from '@angular/router';
import { LaunchNavigator, LaunchNavigatorOptions } from '@ionic-native/launch-navigator/ngx';

@Component({
  selector: 'app-basketball',
  templateUrl: './basketball.page.html',
  styleUrls: ['./basketball.page.scss'],
})
export class BasketballPage implements OnInit {

  basketballs: any = [];
	limit: number = 13; // LIMIT GET PERDATA
	start: number = 0;

  constructor(
    private router: Router,
		private postPvdr: PostProvider,
		private launchNavigator: LaunchNavigator,
  ) { }

  navme(address)
  {
    this.launchNavigator.navigate(address);
  }

  ionViewWillEnter() {
		this.basketballs = [];
		this.start = 0;
		this.loadSportCenter();
  }
  
  loadData(event: any) {
		this.start += this.limit;
		setTimeout(() => {
			this.loadSportCenter().then(() => {
				event.target.complete();
			});
		}, 500);
  }
  
  loadSportCenter() {
		return new Promise(resolve => {
			let body = {
				aksi: 'getSportCenterBasketball',
			};

			this.postPvdr.postData(body, 'process-api.php').subscribe(data => {
				for (let basketball of data.result) {
					this.basketballs.push(basketball);
				}
				resolve(true);
			});
		});
	}

  ngOnInit() {
  }

}
